/*
(i) Стили будут применяться ко 
всем классам содержащим *__container
Например header__container, main__container и т.д.
Снипет (HTML): cnt
*/
[class*=__container] {
  max-width: 89.5rem;
  margin: 0 auto;
  padding: 0 1em;
}

@media (min-width: 900px) and (max-width: 1750px) {
  [class*=__container] {
    max-width: 90%;
  }
}

* {
  padding: 0px;
  margin: 0px;
  border: 0px;
}

*,
